import { isFunction, isObject, clone, merge } from '../../utils/typeChecks'

/**
 * ÂÆåÂñÑË¶ÜÁõñÁâ©ÂÆû‰æã
 * @param overlay Ë¶ÜÁõñÁâ©ÂÆû‰æã
 * @param options ÂèÇÊï∞
 */
export function perfectOverlayFunc (overlay, funcs) {
  funcs.forEach(({ key, fn }) => {
    if (isFunction(fn)) {
      overlay[key] = fn
    }
  })
}

/**
 * Ë¶ÜÁõñÁâ©
 */
export default class Overlay {
  constructor ({
    id, chartData, xAxis, yAxis
  }) {
    this._id = id
    this._chartData = chartData
    this._xAxis = xAxis
    this._yAxis = yAxis
    this._styles = null
  }

  /**
   * ÁªòÂà∂
   * @param ctx
   */
  draw (ctx) {}

  /**
   * ËÆæÁΩÆÊ†∑Âºè
   * @param styles
   * @param defaultStyles
   */
  setStyles (styles, defaultStyles) {
    if (!isObject(styles)) {
      return false
    }
    if (!this._styles) {
      this._styles = clone(defaultStyles)
    }
    merge(this._styles, styles)
    return true
  }

  /**
   * Ëé∑Âèñid
   * @return {*}
   */
  id () {
    return this._id
  }

  /**
   * Ëé∑ÂèñÊ†∑Âºè
   * @return {null}
   */
  styles () {
    return this._styles
  }

  /**
   * ËÆæÁΩÆyËΩ¥
   * @param yAxis
   */
  setYAxis (yAxis) {
    if (yAxis) {
      this._yAxis = yAxis
    }
  }

  /**
   * Ê£ÄÊü•Èº†Ê†áÁÇπÊòØÂê¶Âú®ÂõæÂΩ¢‰∏ä
   * @param coordinate
   */
  checkEventCoordinateOn (coordinate) {}

  // -------------------- ‰∫ã‰ª∂ÂºÄÂßã -------------------

  /**
   * ÁÇπÂáª‰∫ã‰ª∂
   * @param id
   * @param points
   * @param event
   */
  onClick ({ id, points, event }) {}

  /**
   * Âè≥Âáª‰∫ã‰ª∂
   * @param id
   * @param points
   * @param event
   */
  onRightClick ({ id, points, event }) {}

  /**
   * Èº†Ê†áËøõÂÖ•‰∫ã‰ª∂
   * @param id
   * @param points
   * @param event
   */
  onMouseEnter ({ id, points, event }) {}

  /**
   * Èº†Ê†áÁ¶ªÂºÄ‰∫ã‰ª∂
   * @param id
   * @param points
   * @param event
   */
  onMouseLeave ({ id, points, event }) {}

  // -------------------- ‰∫ã‰ª∂ÁªìÊùü -------------------
}
